@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Playfair+Display:wght@400;500;600;700&display=swap";
:root {
  --primary-color: #900;
  --secondary-color: #fff;
  --text-color: #000;
  --light-gray: #f8f8f8;
  --medium-gray: #e0e0e0;
  --dark-gray: #333;
  --gold-accent: #d4af37;
  --font-primary: "Playfair Display", "Arial", serif;
  --font-secondary: "Montserrat", "Arial", sans-serif;
  --transition-speed: .3s;
  --box-shadow: 0 5px 15px #00000014;
  --border-radius: 4px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-secondary);
  color: var(--text-color);
  background-color: var(--secondary-color);
  letter-spacing: .3px;
  line-height: 1.6;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-primary);
  letter-spacing: .5px;
  margin-bottom: 1rem;
  font-weight: 600;
}

a {
  color: var(--primary-color);
  transition: color var(--transition-speed);
  text-decoration: none;
}

a:hover {
  color: var(--dark-gray);
}

.btn {
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all var(--transition-speed);
  font-family: var(--font-secondary);
  letter-spacing: .5px;
  text-transform: uppercase;
  border: none;
  padding: .7rem 1.8rem;
  font-size: .9rem;
  font-weight: 500;
  display: inline-block;
}

.btn-primary {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  box-shadow: 0 2px 5px #9003;
}

.btn-primary:hover {
  color: var(--secondary-color);
  background-color: maroon;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #9900004d;
}

.btn-primary:active {
  transform: translateY(0);
  box-shadow: 0 2px 3px #9003;
}

.section-title {
  text-align: center;
  color: var(--primary-color);
  margin: 3rem 0 2rem;
  font-size: 2.2rem;
  font-weight: 700;
  position: relative;
}

.section-title:after {
  content: "";
  background-color: var(--primary-color);
  width: 60px;
  height: 3px;
  margin: .8rem auto;
  display: block;
}

.placeholder-image {
  background-color: var(--light-gray);
  width: 100%;
  height: 200px;
  color: var(--dark-gray);
  border-radius: var(--border-radius);
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.placeholder-image:before {
  content: "";
  background: linear-gradient(45deg, #9900000d, #9900001a);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.language-switcher {
  z-index: 100;
  position: absolute;
  top: 15px;
  left: 15px;
}

.language-switcher .btn {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  background-color: #0000;
  border-radius: 50px;
  padding: .4rem 1rem;
  font-size: .8rem;
}

.language-switcher .btn:hover {
  background-color: var(--primary-color);
  color: var(--secondary-color);
}

.site-header {
  background-color: var(--secondary-color);
  padding: 1.5rem 0;
  position: relative;
  box-shadow: 0 2px 10px #0000000d;
}

.logo h1 {
  color: var(--primary-color);
  letter-spacing: 1px;
  margin: 0;
  font-size: 2rem;
  font-weight: 700;
}

.main-nav {
  justify-content: flex-end;
  display: flex;
}

.nav-links {
  margin: 0;
  list-style: none;
  display: flex;
}

.nav-link {
  color: var(--text-color);
  letter-spacing: .5px;
  padding: .5rem 1.2rem;
  font-size: .95rem;
  font-weight: 500;
  position: relative;
}

.nav-link:after {
  content: "";
  background-color: var(--primary-color);
  width: 0;
  height: 2px;
  transition: all var(--transition-speed);
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.nav-link:hover:after, .nav-link.active:after {
  width: 70%;
}

.nav-link:hover, .nav-link.active {
  color: var(--primary-color);
}

.hero-section {
  background-color: var(--light-gray);
  padding: 6rem 0;
  position: relative;
  overflow: hidden;
}

.hero-section:before {
  content: "";
  z-index: 1;
  background: linear-gradient(45deg, #99000008, #99000012);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.hero-content {
  z-index: 2;
  position: relative;
}

.hero-title {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.2;
}

.hero-description {
  color: var(--dark-gray);
  max-width: 80%;
  margin-bottom: 2.5rem;
  font-size: 1.3rem;
}

.hero-image {
  z-index: 2;
  position: relative;
}

.featured-products {
  padding: 5rem 0;
}

.product-card {
  background-color: var(--secondary-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  transition: all var(--transition-speed);
  margin-bottom: 2.5rem;
  position: relative;
  overflow: hidden;
}

.product-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 25px #0000001a;
}

.product-image {
  position: relative;
  overflow: hidden;
}

.product-image:after {
  content: "";
  background: linear-gradient(#0000, #0000000d);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.product-details {
  padding: 1.8rem;
}

.product-title {
  color: var(--text-color);
  margin-bottom: .7rem;
  font-size: 1.3rem;
  font-weight: 600;
}

.product-price {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  display: block;
}

.add-to-cart {
  text-align: center;
  width: 100%;
}

.special-offers {
  background-color: var(--light-gray);
  padding: 5rem 0;
  position: relative;
}

.special-offers:before {
  content: "";
  background: linear-gradient(45deg, #99000005, #9900000d);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.offer-card {
  background-color: var(--secondary-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  transition: all var(--transition-speed);
  flex-direction: column;
  margin-bottom: 2.5rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.offer-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 25px #0000001a;
}

.offer-details {
  padding: 1.8rem;
}

.offer-title {
  margin-bottom: .7rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.offer-description {
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.offer-includes {
  margin-bottom: 1.5rem;
  padding-left: 0;
  list-style: none;
}

.offer-includes li {
  color: var(--dark-gray);
  margin-bottom: .5rem;
  padding-left: 1.5rem;
  position: relative;
}

.offer-includes li:before {
  content: "✓";
  color: var(--primary-color);
  font-weight: bold;
  position: absolute;
  left: 0;
}

.offer-price {
  margin-bottom: 1.5rem;
}

.original-price {
  color: var(--dark-gray);
  margin-right: .8rem;
  font-size: 1.1rem;
  text-decoration: line-through;
}

.discount-price {
  color: var(--primary-color);
  font-size: 1.3rem;
  font-weight: 600;
}

.blog-preview {
  padding: 5rem 0;
}

.blog-card {
  background-color: var(--secondary-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  transition: all var(--transition-speed);
  flex-direction: column;
  height: 100%;
  margin-bottom: 2.5rem;
  display: flex;
  overflow: hidden;
}

.blog-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 25px #0000001a;
}

.blog-image {
  position: relative;
  overflow: hidden;
}

.blog-details {
  flex-direction: column;
  flex-grow: 1;
  padding: 1.8rem;
  display: flex;
}

.blog-title {
  margin-bottom: .7rem;
  font-size: 1.3rem;
  font-weight: 600;
}

.blog-excerpt {
  color: var(--dark-gray);
  flex-grow: 1;
  margin-bottom: 1.5rem;
}

.read-more {
  margin-bottom: 1.2rem;
  font-weight: 600;
  display: inline-block;
  position: relative;
}

.read-more:after {
  content: "";
  background-color: var(--primary-color);
  width: 0;
  height: 2px;
  transition: width var(--transition-speed);
  position: absolute;
  bottom: -3px;
  left: 0;
}

.read-more:hover:after {
  width: 100%;
}

.blog-meta {
  color: var(--dark-gray);
  border-top: 1px solid var(--medium-gray);
  justify-content: space-between;
  padding-top: 1rem;
  font-size: .9rem;
  display: flex;
}

.blog-likes, .blog-comments {
  cursor: pointer;
  transition: color var(--transition-speed);
}

.blog-likes:hover, .blog-comments:hover {
  color: var(--primary-color);
}

.cart-preview {
  background-color: var(--secondary-color);
  z-index: 1000;
  width: 400px;
  height: 100vh;
  transition: right var(--transition-speed);
  flex-direction: column;
  display: flex;
  position: fixed;
  top: 0;
  right: -400px;
  box-shadow: -5px 0 15px #0000001a;
}

.cart-preview.active {
  right: 0;
}

.cart-header {
  border-bottom: 1px solid var(--medium-gray);
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  display: flex;
}

.cart-header h3 {
  margin: 0;
  font-size: 1.5rem;
}

.close-cart {
  cursor: pointer;
  color: var(--dark-gray);
  transition: color var(--transition-speed);
  background: none;
  border: none;
  font-size: 1.8rem;
}

.close-cart:hover {
  color: var(--primary-color);
}

.cart-items {
  flex: 1;
  padding: 1.5rem;
  overflow-y: auto;
}

.cart-item {
  border-bottom: 1px solid var(--medium-gray);
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  display: flex;
}

.cart-item-details h4 {
  margin-bottom: .3rem;
  font-size: 1.1rem;
}

.remove-item {
  color: var(--primary-color);
  cursor: pointer;
  transition: color var(--transition-speed);
  background: none;
  border: none;
  font-size: 1.5rem;
}

.remove-item:hover {
  color: var(--dark-gray);
}

.empty-cart-message {
  text-align: center;
  color: var(--dark-gray);
  margin-top: 3rem;
  font-style: italic;
}

.cart-footer {
  border-top: 1px solid var(--medium-gray);
  padding: 1.5rem;
}

.cart-total {
  justify-content: space-between;
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  display: flex;
}

.checkout-btn {
  width: 100%;
}

.site-footer {
  background-color: var(--dark-gray);
  color: var(--secondary-color);
  padding: 4rem 0 2rem;
}

.site-footer h3 {
  color: var(--secondary-color);
  margin-bottom: 1.5rem;
  padding-bottom: .8rem;
  font-size: 1.5rem;
  position: relative;
}

.site-footer h3:after {
  content: "";
  background-color: var(--primary-color);
  width: 40px;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer-links {
  padding: 0;
  list-style: none;
}

.footer-links li {
  margin-bottom: .8rem;
}

.footer-links a {
  color: var(--secondary-color);
  transition: color var(--transition-speed);
}

.footer-links a:hover {
  color: var(--medium-gray);
}

.copyright {
  text-align: center;
  color: #ffffffb3;
  border-top: 1px solid #ffffff1a;
  margin-top: 3rem;
  padding-top: 1.5rem;
  font-size: .9rem;
}

.page-title {
  background-color: var(--light-gray);
  text-align: center;
  padding: 3rem 0;
  position: relative;
}

.page-title:before {
  content: "";
  background: linear-gradient(45deg, #99000008, #99000012);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.page-title .section-title {
  margin: 0;
}

.category-filters {
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem 0;
  display: flex;
}

.category-filter {
  border: 1px solid var(--medium-gray);
  cursor: pointer;
  transition: all var(--transition-speed);
  background: none;
  border-radius: 50px;
  margin: 0 .5rem 1rem;
  padding: .5rem 1.5rem;
  font-size: .9rem;
}

.category-filter:hover, .category-filter.active {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border-color: var(--primary-color);
}

.products-grid, .blog-posts {
  padding: 3rem 0 5rem;
}

.contact-info {
  padding: 3rem 0;
}

.contact-card {
  background-color: var(--secondary-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  text-align: center;
  margin-bottom: 2rem;
  padding: 2rem;
}

.contact-card h3 {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
}

.contact-card p {
  margin-bottom: 1.5rem;
}

.contact-form {
  background-color: var(--light-gray);
  padding: 3rem 0 5rem;
}

.faq-section {
  padding: 5rem 0;
}

.accordion-item {
  border-radius: var(--border-radius);
  border: none;
  margin-bottom: 1rem;
  overflow: hidden;
}

.accordion-header {
  background-color: var(--secondary-color);
}

.accordion-button {
  font-family: var(--font-primary);
  background-color: var(--secondary-color);
  color: var(--text-color);
  box-shadow: none;
  padding: 1.5rem;
  font-weight: 600;
}

.accordion-button:not(.collapsed) {
  color: var(--primary-color);
  background-color: var(--secondary-color);
  box-shadow: none;
}

.accordion-button:focus {
  box-shadow: none;
  border-color: var(--medium-gray);
}

.accordion-body {
  background-color: var(--light-gray);
  padding: 1.5rem;
}

.blog-post-content {
  padding: 4rem 0 6rem;
}

.blog-post {
  margin-bottom: 3rem;
}

.blog-post-header {
  margin-bottom: 2rem;
}

.blog-post-title {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.blog-post-meta {
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  font-size: .95rem;
  display: flex;
}

.blog-post-date {
  margin-right: 1.5rem;
}

.blog-post-category {
  padding-left: 1.5rem;
  position: relative;
}

.blog-post-category:before {
  content: "•";
  position: absolute;
  left: .5rem;
}

.blog-post-featured-image {
  margin-bottom: 2rem;
}

.blog-post-featured-image .placeholder-image {
  height: 400px;
}

.blog-post-body {
  margin-bottom: 2rem;
  font-size: 1.1rem;
  line-height: 1.8;
}

.blog-post-body h1, .blog-post-body h2, .blog-post-body h3 {
  color: var(--primary-color);
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.blog-post-body p {
  margin-bottom: 1.5rem;
}

.blog-post-body ul, .blog-post-body ol {
  margin-bottom: 1.5rem;
  padding-left: 1.5rem;
}

.blog-post-body li {
  margin-bottom: .5rem;
}

.blog-post-actions {
  margin-bottom: 3rem;
  display: flex;
}

.like-button, .share-button {
  border: 1px solid var(--medium-gray);
  color: var(--dark-gray);
  transition: all var(--transition-speed);
  background: none;
  margin-right: 1rem;
}

.like-button:hover, .share-button:hover {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border-color: var(--primary-color);
}

.comments-section {
  margin-bottom: 3rem;
}

.comments-section h3 {
  border-bottom: 1px solid var(--medium-gray);
  margin-bottom: 2rem;
  padding-bottom: 1rem;
}

.comment-form {
  margin-bottom: 3rem;
}

.comment-form h4 {
  margin-bottom: 1.5rem;
}

.comments-list {
  margin-top: 2rem;
}

.comment {
  background-color: var(--light-gray);
  border-radius: var(--border-radius);
  margin-bottom: 1.5rem;
  padding: 1.5rem;
}

.comment-header {
  justify-content: space-between;
  margin-bottom: 1rem;
  display: flex;
}

.commenter-name {
  margin: 0;
  font-size: 1.1rem;
}

.comment-date {
  color: var(--dark-gray);
  font-size: .9rem;
}

.no-comments-message {
  text-align: center;
  color: var(--dark-gray);
  padding: 2rem 0;
  font-style: italic;
}

.related-posts {
  margin-top: 4rem;
}

.related-posts h3 {
  margin-bottom: 2rem;
}

.blog-sidebar {
  margin-top: 4rem;
}

.sidebar-widget {
  background-color: var(--light-gray);
  border-radius: var(--border-radius);
  margin-bottom: 2rem;
  padding: 1.5rem;
}

.sidebar-widget h3 {
  border-bottom: 1px solid var(--medium-gray);
  margin-bottom: 1.5rem;
  padding-bottom: .8rem;
  font-size: 1.3rem;
}

.categories-list, .recent-posts-list {
  padding: 0;
  list-style: none;
}

.categories-list li, .recent-posts-list li {
  border-bottom: 1px solid var(--medium-gray);
  margin-bottom: .8rem;
  padding-bottom: .8rem;
}

.categories-list li:last-child, .recent-posts-list li:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.post-date {
  color: var(--dark-gray);
  margin-top: .3rem;
  font-size: .85rem;
  display: block;
}

.checkout-section {
  padding: 4rem 0 6rem;
}

.checkout-form-container {
  background-color: var(--secondary-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: 2rem;
  padding: 2rem;
}

.checkout-form-container h2 {
  border-bottom: 1px solid var(--medium-gray);
  color: var(--primary-color);
  margin-bottom: 2rem;
  padding-bottom: 1rem;
}

.order-summary {
  background-color: var(--light-gray);
  border-radius: var(--border-radius);
  padding: 2rem;
  position: sticky;
  top: 2rem;
}

.order-summary h2 {
  border-bottom: 1px solid var(--medium-gray);
  color: var(--primary-color);
  margin-bottom: 2rem;
  padding-bottom: 1rem;
}

.order-items {
  margin-bottom: 2rem;
}

.order-item {
  border-bottom: 1px solid var(--medium-gray);
  justify-content: space-between;
  padding: 1rem 0;
  display: flex;
}

.order-item-details h4 {
  margin-bottom: .3rem;
  font-size: 1rem;
}

.order-item-total {
  font-weight: 600;
}

.order-totals {
  border-top: 2px solid var(--medium-gray);
  padding-top: 1.5rem;
}

.subtotal, .shipping, .total {
  justify-content: space-between;
  margin-bottom: 1rem;
  display: flex;
}

.total {
  color: var(--primary-color);
  border-top: 1px solid var(--medium-gray);
  margin-top: 1rem;
  padding-top: 1rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.offers-section {
  padding: 4rem 0 6rem;
}

.bulk-discounts {
  background-color: var(--light-gray);
  padding: 4rem 0;
}

.discount-table-container {
  background-color: var(--secondary-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: 2rem;
  padding: 2rem;
}

.discount-table {
  border-collapse: collapse;
  width: 100%;
}

.discount-table th, .discount-table td {
  text-align: center;
  border-bottom: 1px solid var(--medium-gray);
  padding: 1rem;
}

.discount-table th {
  background-color: var(--light-gray);
  font-weight: 600;
}

.bulk-order-note {
  text-align: center;
  color: var(--dark-gray);
  font-style: italic;
}

@media (width <= 991px) {
  .hero-title {
    font-size: 2.8rem;
  }

  .hero-description {
    max-width: 100%;
    font-size: 1.1rem;
  }

  .blog-sidebar {
    margin-top: 0;
  }
}

@media (width <= 768px) {
  .site-header {
    padding: 1rem 0;
  }

  .logo {
    text-align: center;
    margin-bottom: 1rem;
  }

  .main-nav {
    justify-content: center;
  }

  .nav-links {
    flex-wrap: wrap;
    justify-content: center;
  }

  .nav-link {
    padding: .5rem .8rem;
    font-size: .9rem;
  }

  .hero-section {
    text-align: center;
    padding: 3rem 0;
  }

  .hero-title {
    font-size: 2.2rem;
  }

  .hero-description {
    margin-left: auto;
    margin-right: auto;
    font-size: 1rem;
  }

